
.header-small {
  width: 100%;
  height: 60px;
  position: fixed;
  z-index: 1;
  -webkit-transition-duration: 600ms;
       -o-transition-duration: 600ms;
          transition-duration: 600ms;
}
.header-small img {
  float: right;
  width: 105px;
  margin-right: 10px;
}
.header-small .container-menu {
  width: 40px;
  height: 40px;
  margin-top: 10px;
  float: left;
  -webkit-transition-duration: 600ms;
       -o-transition-duration: 600ms;
          transition-duration: 600ms;
  margin-left: 20px;
  cursor: pointer;
}
.header-small .container-menu .child {
  position: absolute;
  top: 28px;
  width: 30px;
  height: 4px;
  border-radius: 2px;
  -webkit-transition-duration: 400ms;
       -o-transition-duration: 400ms;
          transition-duration: 400ms;
}
.header-small .container-menu .child.one { -webkit-transform: translateY(-10px); -ms-transform: translateY(-10px); transform: translateY(-10px) }
.header-small .container-menu .child.two { -webkit-transform: translateY(0px); -ms-transform: translateY(0px); transform: translateY(0px) }
.header-small .container-menu .child.three { -webkit-transform: translateY(10px); -ms-transform: translateY(10px); transform: translateY(10px) }

.header-small .container-menu.actived .child.one { -webkit-transform: translateY(0px) rotate(225deg); -ms-transform: translateY(0px) rotate(225deg); transform: translateY(0px) rotate(225deg) }
.header-small .container-menu.actived .child.two { opacity: 0 }
.header-small .container-menu.actived .child.three { -webkit-transform: translateY(0px) rotate(-225deg); -ms-transform: translateY(0px) rotate(-225deg); transform: translateY(0px) rotate(-225deg) }



