.header-big .main-menu .li-lvl-1 {
  float: left;
  padding: 37px 0px 37px 40px;
}
.header-big .main-menu .li-lvl-1 i { margin-right: 8px }
.header-big .main-menu i.nos-destinations { -webkit-transform: rotate(-45deg); -ms-transform: rotate(-45deg); transform: rotate(-45deg) }
.header-big .main-menu i.nos-promotions { -webkit-transform: rotate(90deg); -ms-transform: rotate(90deg); transform: rotate(90deg) }


.header-big .main-menu .sub-menu-1 {
  margin-top: 32px;
  -webkit-animation: show-submenu-1 500ms;
          animation: show-submenu-1 500ms;
}
@-webkit-keyframes show-submenu-1 {
  0% {
    opacity: 0;
    -webkit-transform: scale(0.95);
            transform: scale(0.95);
  } 100% {
    opacity: 1;
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}
@keyframes show-submenu-1 {
  0% {
    opacity: 0;
    -webkit-transform: scale(0.95);
            transform: scale(0.95);
  } 100% {
    opacity: 1;
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}
.header-big .main-menu .ul-lvl-3 {
  -webkit-transition-duration: 400ms;
       -o-transition-duration: 400ms;
          transition-duration: 400ms;
  overflow: hidden;
}

.header-big .main-menu .fake-arrow {
  width: 20px;
  height: 20px;
  -webkit-transform: rotate(45deg) translateX(40px) translateY(-23px);
      -ms-transform: rotate(45deg) translateX(40px) translateY(-23px);
          transform: rotate(45deg) translateX(40px) translateY(-23px);
  background: white;
  z-index: 0;
}
.header-big .main-menu .ul-lvl-2 {
  z-index: 1;
  background: white;
  padding: 10px;
  -webkit-box-shadow: 0 0 4px rgba(100,100,100,0.25);
          box-shadow: 0 0 4px rgba(100,100,100,0.25);
}
.header-big .main-menu .li-lvl-2 {
  padding: 5px 0;
  color: #333;
  font-size: 14px;
}
.header-big .main-menu .li-lvl-3:first-child {
  padding-top: 20px;
}
.header-big .main-menu .li-lvl-3 {
  padding: 5px 0;
  color: #888;
  font-size: 12px;
  margin-left: 20px;
  padding: 6px 0
}
.header-big .main-menu .li-lvl-2 i.fa-chevron-up {
  float: right;
}



