
.left-panel {
  position: fixed;
  width: 280px;
  min-height: 100%;
  background: #eee;
  z-index: 1;
  top: 0px;
  left: -280px;
  -webkit-transition-duration: 600ms;
       -o-transition-duration: 600ms;
          transition-duration: 600ms;
}
.left-panel.actived {
  left: 0px;
}
.left-panel ul.main-menu {
  margin-top: 30px;
}



